import {
  ComponentProps,
  Streamlit,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { useEffect } from "react"
import {
  makeStyles,
  createStyles,
  Theme,
  Tooltip,
  lighten,
} from "@material-ui/core"
import numeral from "numeral"

interface FlashCardProps {
  title: string
  primaryText: string
  secondaryText?: string
  aspectRatio?: [number, number]
  formatter?: string
}

const useStyles = makeStyles<Theme, ComponentProps>(
  createStyles({
    root: {
      fontFamily: ({ theme }) => theme?.font,
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
    },
    header: {
      fontSize: "1.25rem",
      fontWeight: 500,
      width: "100%",
      textAlign: "left",
      overflow: "hidden",
      textOverflow: "ellipsis",
      whiteSpace: "nowrap",
    },
    container: {
      flex: 1,
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      alignItems: "center",
      maxWidth: "100%",
    },
    primary: {
      color: ({ theme }) => theme?.textColor,
      fontWeight: 600,
      fontSize: `${12}vw`,
      maxWidth: "100%",
      textTransform: "uppercase",
      overflow: "hidden",
      textOverflow: "ellipsis",
      whiteSpace: "nowrap",
    },
    secondary: {
      color: ({ theme }) => lighten(theme?.textColor || "#333", 0.3),
    },
    tooltip: {
      margin: 0,
      fontSize: 16,
    },
  }),
  { name: "FlashCard" }
)

const FlashCard: React.FC<ComponentProps> = (props) => {
  const { args, width } = props
  const {
    primaryText,
    secondaryText,
    aspectRatio = [16, 9],
    formatter = "0,0",
  } = args as FlashCardProps

  const classes = useStyles(props)

  const height = (width * aspectRatio[1] * 1.0) / aspectRatio[0]

  const format = (text: string) => {
    if (!isNaN(parseFloat(text)))
      return numeral(parseFloat(text)).format(formatter)
    return text
  }

  useEffect(() => {
    Streamlit.setFrameHeight(height)
    Streamlit.setComponentValue([width, height])
  }, [width, height])

  return (
    <div className={classes.root} style={{ height }}>
      <div className={classes.container}>
        <Tooltip
          classes={{ tooltip: classes.tooltip }}
          placement="top"
          interactive
          leaveDelay={1000}
          arrow
          title={
            secondaryText ? `${primaryText} ${secondaryText}` : primaryText
          }
        >
          <div className={classes.primary}>{format(primaryText)}</div>
        </Tooltip>
        {secondaryText && (
          <div className={classes.secondary}>{secondaryText}</div>
        )}
      </div>
    </div>
  )
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(FlashCard)
